<template>
  <el-table
    ref="tableRef"
    class="vp-raw liv-data-table"
    table-layout="auto"
    :tooltip-options="{
      placement: 'right',
    }"
    :data="data"
    v-bind="pick(globalProps, Object.keys(tableProps))">
    <template v-for="field in validFields" :key="field.prop">
      <el-table-column
        v-if="!field.fieldType || field.fieldType === 'text'"
        class="liv-table-column__text"
        :show-overflow-tooltip="{
          popperClass: 'liv-table-column__tooltip',
        }"
        v-bind="pick(field as TextTableColumnField<T>, Object.keys(tableColumnProps))">
      </el-table-column>
      <el-table-column
        v-else-if="field.fieldType === 'ciphertext'"
        v-bind="pick(field as TextTableColumnField<T>, Object.keys(tableColumnProps))"
        :showOverflowTooltip="false">
        <template #default="scope: DataTableScope<T>">
          <div class="liv-table-column__ciphertext">
            <el-tooltip
              v-if="field.showOverflowTooltip !== false"
              popper-class="liv-table-column__tooltip"
              :content="
                verifyInfoMap[scope.$index][field.prop].decrypted
                  ? verifyInfoMap[scope.$index][field.prop].plaintext
                  : (getFormatter(field, scope) as string)
              "
              placement="right">
              <div class="liv-table-column__ciphertext--tooltip">
                {{
                  verifyInfoMap[scope.$index][field.prop].decrypted
                    ? verifyInfoMap[scope.$index][field.prop].plaintext
                    : getFormatter(field, scope)
                }}
              </div>
            </el-tooltip>
            <div v-else>
              {{
                verifyInfoMap[scope.$index][field.prop].decrypted
                  ? verifyInfoMap[scope.$index][field.prop].plaintext
                  : getFormatter(field, scope)
              }}
            </div>
            <el-icon
              v-if="
                verifyInfoMap[scope.$index][field.prop].decrypted
                  ? verifyInfoMap[scope.$index][field.prop].plaintext
                  : getFormatter(field, scope)
              ">
              <View
                v-if="verifyInfoMap[scope.$index][field.prop].decrypted"
                @click="resetCiphertext(scope.$index, field)" /><Hide
                v-else
                @click="verifyInfoMap[scope.$index][field.prop].visible = true" />
            </el-icon>
          </div>
          <Teleport to="body">
            <verify
              v-if="verifyInfoMap[scope.$index][field.prop].visible"
              v-bind="omit(field, Object.keys(tableColumnProps))"
              :type="field.type"
              v-model="verifyInfoMap[scope.$index][field.prop].visible"
              @success="verifySuccess($event, scope, field)"></verify>
          </Teleport>
        </template>
      </el-table-column>
      <el-table-column
        v-else-if="field.fieldType === 'button'"
        v-bind="pick(omit(field, 'type'), Object.keys(tableColumnProps))">
        <template #default="scope: DataTableScope<T>">
          <el-button
            class="liv-table-column__button"
            plain
            size="small"
            v-bind="omit(field, 'type', 'loading', 'onClick', ...Object.keys(tableColumnProps))"
            :type="
              typeof field.type === 'function'
                ? field.type(scope.row, scope.$index, scope.row[field.prop])
                : field.type
            "
            :loading="
              buttonLoadings.find(item => item[0] === scope.row && item[1] === field.prop)
                ? true
                : false
            "
            @click="buttonOnClick(field, scope)"
            >{{ field.name ?? getFormatter(field, scope) }}</el-button
          >
        </template>
      </el-table-column>
      <el-table-column
        v-else-if="field.fieldType === 'switch'"
        v-bind="pick(field, Object.keys(tableColumnProps))">
        <template #default="scope: DataTableScope<T>">
          <el-switch
            class="liv-table-column__switch"
            v-model="scope.row[field.prop]"
            size="small"
            v-bind="
              omit(field, 'beforeChange', 'onChange', 'loading', ...Object.keys(tableColumnProps))
            "
            :loading="
              switchLoadings.find(item => item[0] === scope.row && item[1] === field.prop)
                ? true
                : false
            "
            :before-change="() => switchBeforeChange(field, scope)"
            @change="
              field.onChange?.($event, scope.row, scope.$index, scope.row[field.prop])
            "></el-switch>
        </template>
      </el-table-column>
      <el-table-column
        v-else-if="field.fieldType === 'tag'"
        v-bind="pick(omit(field, 'type'), Object.keys(tableColumnProps))">
        <template #default="scope: DataTableScope<T>">
          <div class="liv-table-column__tags" v-if="Array.isArray(scope.row[field.prop])">
            <el-tag
              v-for="tag in scope.row[field.prop]"
              class="liv-table-column__tag"
              v-bind="omit(field, Object.keys(tableColumnProps))"
              :type="
                typeof field.type === 'function'
                  ? field.type(scope.row, scope.$index, tag)
                  : field.type
              "
              >{{ tag }}</el-tag
            >
          </div>
          <el-tag
            v-else
            class="liv-table-column__tag"
            v-bind="omit(field, Object.keys(tableColumnProps))"
            :type="
              typeof field.type === 'function'
                ? field.type(scope.row, scope.$index, scope.row[field.prop])
                : field.type
            "
            >{{ getFormatter(field, scope) }}</el-tag
          >
        </template>
      </el-table-column>
      <el-table-column
        v-else-if="field.fieldType === 'image'"
        class="liv-table-column__image"
        v-bind="pick(field, Object.keys(tableColumnProps))">
        <template #default="scope: DataTableScope<T>">
          <template v-if="scope.row[field.prop]">
            <el-popover
              v-for="(image, index) in (handleThumbUrls ?? handleFileUrls)?.(scope.row[field.prop])"
              :key="image"
              placement="top-start"
              trigger="hover"
              @before-enter="
                imagePopover = [imagePopover[2], imagePopover[3], scope.$index, index]
              ">
              <template #reference>
                <el-image
                  :preview-src-list="handleFileUrls?.(scope.row[field.prop])"
                  :src="image"
                  :initial-index="index"
                  fit="cover"
                  lazy
                  preview-teleported
                  class="liv-table-column__image--thumb" />
              </template>
              <el-image
                v-if="
                  (imagePopover[0] === scope.$index && imagePopover[1] === index) ||
                  (imagePopover[2] === scope.$index && imagePopover[3] === index)
                "
                :preview-src-list="handleFileUrls?.(scope.row[field.prop])"
                :src="image"
                :initial-index="index"
                fit="cover"
                preview-teleported
                class="liv-table-column__image--preview" />
            </el-popover>
          </template>
          <template v-else>无</template>
        </template>
      </el-table-column>
      <el-table-column
        v-else-if="field.fieldType === 'qrcode'"
        v-bind="pick(field, Object.keys(tableColumnProps))">
        <template #default="scope: DataTableScope<T>">
          <el-popover placement="top-start" trigger="hover">
            <template #reference>
              <qr-code
                class="liv-table-column__qrcode"
                :content="scope.row[field.prop]"
                :size="30"
                v-bind="omit(field, Object.keys(tableColumnProps))"
                :options="{ width: 400, ...(field.options ?? {}) }" />
            </template>
            <qr-code
              class="liv-table-column__qrcode--preview"
              :content="scope.row[field.prop]"
              :size="125"
              v-bind="omit(field, Object.keys(tableColumnProps))"
              :options="{ width: 400, ...(field.options ?? {}) }" />
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
        v-else-if="field.fieldType === 'slot'"
        class="liv-table-column__slot"
        v-bind="pick(field, Object.keys(tableColumnProps))">
        <template #default="scope: DataTableScope<T>">
          <slot :name="field.prop" v-bind="scope"></slot>
        </template>
      </el-table-column>
    </template>
    <slot></slot>
    <el-table-column v-if="validButtons.length > 0" fixed="right" label="操作">
      <template #default="scope: DataTableScope<T>">
        <div class="liv-table-column__operation">
          <el-button
            v-for="button in getShowButtons(scope.row, scope.$index).slice(
              0,
              propsPoxy.showButtonCount,
            )"
            :key="button.name"
            plain
            size="small"
            @click="button.onClick?.(scope.row, scope.$index)"
            v-bind="omit(button, ['onClick'])"
            >{{ button.name }}</el-button
          >
          <el-popover
            placement="left"
            v-if="
              getShowButtons(scope.row, scope.$index).slice(propsPoxy.showButtonCount).length > 0
            ">
            <template #reference>
              <el-icon class="liv-table-column__operation--more"><MoreFilled /></el-icon>
            </template>
            <div class="liv-table-column__operation--container">
              <el-button
                plain
                v-for="button in getShowButtons(scope.row, scope.$index).slice(
                  propsPoxy.showButtonCount,
                )"
                :key="button.name"
                size="small"
                @click="button.onClick?.(scope.row, scope.$index)"
                v-bind="omit(button, ['onClick'])"
                >{{ button.name }}</el-button
              >
            </div>
          </el-popover>
        </div>
      </template>
    </el-table-column>
    <template v-if="!$slots.empty" #empty>
      <el-empty description="暂无数据" />
    </template>
    <template #empty v-else>
      <slot name="empty"></slot>
    </template>
  </el-table>
</template>

<script lang="ts" setup generic="T extends Record<string | number | symbol, any>">
import {
  watchEffect,
  useAttrs,
  ref,
  toValue,
  computed,
  type Ref,
  unref,
  type VNode,
  inject,
} from 'vue';
import {
  ElTable,
  ElTableColumn,
  ElButton,
  ElSwitch,
  ElPopover,
  ElTag,
  ElImage,
  ElIcon,
  ElEmpty,
  ElTooltip,
} from 'element-plus';
// @ts-ignore
import tableProps from 'element-plus/es/components/table/src/table/defaults.mjs';
// @ts-ignore
import tableColumnProps from 'element-plus/es/components/table/src/table-column/defaults.mjs';
import { View, Hide, MoreFilled } from '@element-plus/icons-vue';
import { omit, pick } from 'lodash-es';
import type {
  ButtonTableColumnField,
  CiphertextTableColumnField,
  DataTableButton,
  DataTableField,
  DataTableFullProps,
  DataTableProps,
  DataTableScope,
  SwitchTableColumnField,
  TextTableColumnField,
} from './data-table';
import Verify from '../../verify/src/verify.vue';
import QrCode from '../../qr-code/src/qr-code.vue';
import {
  MakeRequired,
  checkAuthoritys,
  getPropsPoxy,
  injectCheckAuthority,
  injectHandleFileUrls,
  injectHandleThumbUrls,
  sortByOrder,
} from '@liv-web/utils';
import { DATA_TABLE_KEY } from '@liv-web/constants';
import type { VerifyResult } from '../../verify';

/**
 * 组件配置
 */
defineOptions({
  name: 'LivDataTable',
});

/**
 * 注入校验权限方法
 */
const checkAuthority = injectCheckAuthority();

/**
 * 注入处理文件地址方法
 */
const handleFileUrls = injectHandleFileUrls();

/**
 * 注入处理缩略图地址方法
 */
const handleThumbUrls = injectHandleThumbUrls();

/**
 * 注入全局属性
 */
const globalProps = inject(DATA_TABLE_KEY, {});

/**
 * 组件属性
 */
const props = withDefaults(defineProps<DataTableProps<T>>(), {
  data: () => [] as T[],
  showButtonCount: () => 3,
  buttons: () => [] as DataTableButton<T>[],
});

/**
 * 属性代理
 */
const propsPoxy = getPropsPoxy<MakeRequired<DataTableFullProps<T>, 'showButtonCount'>>(
  props,
  globalProps,
);

/**
 * 组件参数
 */
const attrs = useAttrs();

/**
 * 有效的fields配置项
 */
const validFields = computed<DataTableField<T>[]>(() => {
  return (toValue(props.fields) as DataTableField<T>[]).filter(field => {
    return checkAuthoritys(checkAuthority, field.authority);
  });
});

/**
 * 内置操作按钮
 */
const innerButtons = computed<DataTableButton<T>[]>(() => {
  return [initInnerButton('onDetail'), initInnerButton('onEdit'), initInnerButton('onDelete')];
});

/**
 * 需要展示的操作按钮
 */
const validButtons = computed(() => {
  return innerButtons.value
    .concat(props.buttons)
    .filter(button => {
      return checkAuthoritys(checkAuthority, button.authority) && button.show !== false;
    })
    .sort((a, b) => sortByOrder(a.order, b.order));
});

/**
 * 内置按钮默认属性映射
 */
const innerButtonMap: {
  onDetail: DataTableButton<T>;
  onEdit: DataTableButton<T>;
  onDelete: DataTableButton<T>;
} = {
  onDetail: {
    name: '详情',
    type: 'primary',
    order: 10,
  },
  onEdit: {
    name: '编辑',
    type: 'warning',
    order: 20,
  },
  onDelete: {
    name: '删除',
    type: 'danger',
    order: 30,
  },
};

/**
 * 初始化内置按钮
 * @param type 需要初始化的内置按钮类型
 * @returns { DataTableButton<T> } 表格右侧列操作按钮
 */
function initInnerButton(type: 'onDetail' | 'onEdit' | 'onDelete'): DataTableButton<T> {
  const button: DataTableButton<T> = {
    ...innerButtonMap[type],
  };
  if (props[type]) {
    // 有点击事件但没有拼接权限标识
    button.onClick = props[type];
  } else {
    const key = Object.keys(attrs).find(key => key.startsWith(type));
    const regex = /\((.*?)\)/;
    const matches = key?.match(regex);
    if (matches && matches[1]) {
      // 有点击事件并且拼接了权限标识
      button.onClick = attrs[key!] as DataTableButton<T>['onClick'];
      button.authority = matches[1].split('|');
    } else {
      button.show = false;
    }
  }
  return button;
}

/**
 * 获取需要展示的按钮
 * @param row 行数据
 * @param index 数据索引
 * @returns { DataTableButton<T>[] } 表格右侧列操作按钮集合
 */
function getShowButtons(row: T, index: number): DataTableButton<T>[] {
  return validButtons.value.filter(button => {
    if (typeof button.show === 'function') {
      return button.show(row, index);
    } else {
      return button.show !== false;
    }
  });
}

/**
 * 获取格式化后的内容
 * @param field 表格字段配置
 * @param scope 表格插槽作用域数据
 * @return { VNode | string } 格式化后的字符串或虚拟节点
 */
function getFormatter(field: DataTableField<T>, scope: DataTableScope<T>): VNode | string {
  return field.formatter
    ? field?.formatter(scope.row, scope.column, scope.row[field.prop!], scope.$index)
    : scope.row[field.prop!];
}

/* 按钮类型 */

/**
 * 记录按钮的loading状态
 */
const buttonLoadings: Ref<[T, keyof T][]> = ref([]);

/**
 * 表单数据变更后重新初始化按钮列的加载状态
 */
watchEffect(() => {
  buttonLoadings.value =
    props.data?.reduce(
      (pre, cur) => {
        const scope = buttonLoadings.value.find(item => item[0].row === cur);
        if (scope) {
          pre.push(scope);
        }
        return pre;
      },
      [] as [T, keyof T][],
    ) ?? [];
});

/**
 * 扩展el-button的onClick方法
 * @param field 表格字段配置
 * @param scope 表格插槽作用域数据
 */
function buttonOnClick(field: ButtonTableColumnField<T>, scope: DataTableScope<T>) {
  // TODO 待优化，后续版本onClick属性将不再支持传入ref类型
  const result = unref(field.onClick)?.(scope.row, scope.$index, scope.row[field.prop]) ?? true;
  if (result instanceof Promise) {
    if (!buttonLoadings.value.find(item => item[0] === scope.row && item[1] === field.prop)) {
      buttonLoadings.value.push([scope.row, field.prop]);
    }
    result.finally(() => {
      const index = buttonLoadings.value.map(item => item[0]).indexOf(scope.row);
      if (index !== -1) {
        buttonLoadings.value.splice(index, 1);
      }
    });
  }
}

/**
 * 验证码组件信息
 */
interface VerifyInfo {
  /**
   * 是否已解密
   */
  decrypted: boolean;
  /**
   * 验证码组件可见状态
   */
  visible: boolean;
  /**
   * 明文内容
   */
  plaintext: string;
}

type TableVerifyInfo = {
  [key: string]: VerifyInfo;
}[];

/**
 * 加密文本表格列对应的验证码组件信息
 */
const verifyInfoMap = ref<TableVerifyInfo>([]);

watchEffect(() => {
  verifyInfoMap.value = props.data.map(_row => {
    const rowVerifyInfo: { [key: string]: VerifyInfo } = {};
    validFields.value
      .filter(field => field.fieldType === 'ciphertext')
      .forEach(field => {
        rowVerifyInfo[field.prop!] = {
          decrypted: false,
          visible: false,
          plaintext: '',
        };
      });
    return rowVerifyInfo;
  });
});

/**
 * 验证码校验成功回调
 * @param params 校验结果
 * @param scope 作用域数据
 * @param field 字段配置项
 */
function verifySuccess(
  params: VerifyResult,
  scope: DataTableScope<T>,
  field: CiphertextTableColumnField<T>,
) {
  const decryptImpl = field.decrypt?.(params, scope.row);
  if (decryptImpl) {
    if (decryptImpl instanceof Promise) {
      decryptImpl.then(res => {
        verifyInfoMap.value[scope.$index][field.prop].decrypted = true;
        verifyInfoMap.value[scope.$index][field.prop].plaintext = res;
      });
    } else if (typeof decryptImpl === 'string') {
      verifyInfoMap.value[scope.$index][field.prop].decrypted = true;
      verifyInfoMap.value[scope.$index][field.prop].plaintext = decryptImpl;
    } else {
      console.error('[liv-web/data-table]请检查传入的解密逻辑是否正确！');
    }
  } else {
    console.error('[liv-web/data-table]当fieldType为ciphertext时，需要传入具体的解密逻辑！');
  }
}

//  computed(() => {
//   return props.data.map(_row => {
//     const rowVerifyInfo: { [key: string]: VerifyInfo } = {};
//     validFields.value
//       .filter(field => field.fieldType === 'ciphertext')
//       .forEach(field => {
//         rowVerifyInfo[field.prop!] = {
//           decrypted: false,
//           visible: false,
//           plaintext: '',
//         };
//       });
//     return rowVerifyInfo;
//   });
// });

/**
 * 重置加密文本
 * @param index 需要重置加密文本的数据索引
 * @param field 需要重置加密文本的配置项
 */
function resetCiphertext(index: number, field: CiphertextTableColumnField<T>) {
  const verifyInfo = verifyInfoMap.value[index][field.prop];
  if (verifyInfo) {
    verifyInfo.decrypted = false;
    verifyInfo.plaintext = '';
  }
}

/** 开关类型 */

/**
 * 记录开关列的加载状态
 */
const switchLoadings: Ref<[T, keyof T][]> = ref([]);

/**
 * 表单数据变更后重新初始化开关列的加载状态
 */
watchEffect(() => {
  switchLoadings.value =
    props.data?.reduce(
      (pre, cur) => {
        const scope = switchLoadings.value.find(item => item[0].row === cur);
        if (scope) {
          pre.push(scope);
        }
        return pre;
      },
      [] as [T, keyof T][],
    ) ?? [];
});

/**
 * 扩展el-switch的beforeChange方法
 * @param field 表格字段配置
 * @param scope 表格插槽作用域数据
 * @returns { boolean | Promise<boolean> } 返回`false`或者返回`Promise`且被`reject`则停止切换
 */
function switchBeforeChange(
  field: SwitchTableColumnField<T>,
  scope: DataTableScope<T>,
): boolean | Promise<boolean> {
  const result = field.beforeChange?.(scope.row, scope.$index, scope.row[field.prop]) ?? true;
  if (result instanceof Promise) {
    return new Promise<boolean>(resolve => {
      if (!switchLoadings.value.find(item => item[0] === scope.row && item[1] === field.prop)) {
        switchLoadings.value.push([scope.row, field.prop]);
      }
      result.then(resolve).finally(() => {
        const index = switchLoadings.value.map(item => item[0]).indexOf(scope.row);
        if (index !== -1) {
          switchLoadings.value.splice(index, 1);
        }
      });
    });
  } else {
    return result;
  }
}

/** 图片类型 */

/**
 * 用于实现popover内image组件的懒加载
 */
const imagePopover = ref([-1, -1, -1, -1]);

// TODO 待优化
const tableRef = ref();

defineExpose({ tableRef });
</script>
